import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import image from '../video.mp4';

export default class Home extends Component {
  render() {
    return (
      <main>
        <video className="absolute w-full object-cover" autoPlay muted loop>
          <source src={image} type="video/mp4" />
        </video>
        <section className="relative justify-center min-h-screen pt-44 px-4 lg:pt-48 items-center">
          <h1 className="cursive text-5xl justify-center leading-none lg:leading-snug home-name flex text-white">
            Hello! I am T!
          </h1>
          <p className="cursive text-4xl text-center leading-none justify-center pt-5 lg:leading-snug home-name text-white flex">
            a clumsy teen with a knack for science
          </p>
          <div className="items-center justify-center flex">
            <button className="montserrat text-2xl rounded-full font-extrabold px-2 py-2 my-10 transition duration:500 ease-in-out text-white items-center justify-center">
              <Link
                to={{ pathname: '/about' }}
                className="border-b border-white pb-1 homebutton animate-pulse  mb-32"
              >
                Click me :)
              </Link>
            </button>
          </div>
        </section>
      </main>
    );
  }
}
