import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import image from '../T.jpg';

export default class About extends Component {
  render() {
    return (
      <main>
        <section className="relative justify-center min-h-screen pt-24 items-center text-black lg:pt-28 px-4 aboutbg">
          <h1 className="flex justify-center cursive text-5xl font-bold">
            Hello, I am Thanvanth!
          </h1>
          <h1 className="flex justify-center cursive pt-2 text-xl font-bold lg:text-3xl">
            | Student | Coder | master procrastinator | tryhard humorist |
          </h1>
          <div className="content-center">
            <h1 className="flex justify-left playfair text-4xl ml-5 pt-16 pl-15 lg:ml-16">
              About Me,
            </h1>
            <p className="flex justify-left montserrat font-semibold items-center pt-5 pl-15 ml-4 lg:text-3xl lg:ml-16 lg:mr-16">
              Hi, I am Thanvanth but I go by the name T, a STEM student almost
              always found with a pair of headphones, vibing to music eheh.... I
              code, play games and procrastinate. I also happen to be an
              introverted Physics major who has a lot of non-sensical facts
              stored in his mind
            </p>
          </div>
          <div className="flex pt-10 justify-end mr-10 lg:mr-24 montserrat font-bold text-xl">
            <Link
              className="aboutbutton animate-pulse"
              to={{ pathname: '/gallery' }}
            >
              Click me <i class="arrow right"></i>
            </Link>
          </div>
        </section>
      </main>
    );
  }
}
