import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import { SocialIcon } from 'react-social-icons';

export default function Navbar() {
  return (
    <header className="bg-gray-900">
      <div className="container flex mx-auto justify-between">
        <nav className="flex">
          <NavLink
            to="/"
            exact
            className="inline-flex items-center ml-3 py-5 px-3 mr-7 text-3xl cursive transition duration-500 ease-in-out text-white hover:text-gray-400"
          >
            T
          </NavLink>
          <NavLink
            to="/about"
            className="inline-flex items-center py-3 px-3 my-6 text-white montserrat font-bold transition duration-500 ease-in-out hover:text-gray-400"
          >
            About
          </NavLink>
          <NavLink
            to="/Gallery"
            className="inline-flex items-center py-3 px-3 my-6 text-white montserrat transition duration-500 ease-in-out font-bold hover:text-gray-400"
          >
            Gallery
          </NavLink>
        </nav>
        <div className="py-3 my-5 items-center inline-flex px-3">
          <SocialIcon
            url="https://www.github.com/i-thanvanth"
            target="_blank"
            fgColor="#fff"
            style={{ height: 35, width: 35 }}
            className="mr-3"
          />
          <SocialIcon
            url="https://www.instagram.com/i_thanvanth"
            target="_blank"
            fgColor="#fff"
            style={{ height: 35, width: 35 }}
            className="mr-3"
          />
          <SocialIcon
            url="https://www.twitter.com/i_thanvanth"
            target="_blank"
            fgColor="#fff"
            style={{ height: 35, width: 35 }}
            className="mr-3"
          />
        </div>
      </div>
    </header>
  );
}
