import React, { Component } from 'react';
import LazyLoad from 'react-lazyload';
import i1 from '../images/1.jpg';
import i2 from '../images/2.jpg';
import i3 from '../images/3.jpg';
import i4 from '../images/4.jpg';
import i5 from '../images/5.jpg';
import i6 from '../images/6.jpg';
import i7 from '../images/7.jpeg';
import i8 from '../images/8.jpeg';

export default class Gallery extends Component {
  render() {
    return (
      <main>
        <section className="gallerybg relative justify-center">
          <div className="flex justify-center items-center cursive font-bold text-4xl pt-5">
            Hi, welcome to the Gallery!
          </div>
          <div className="flex justify-center items-center cursive font-bold text-2xl pt-5">
            T also does photography, check out some of his pictures
          </div>
          <div className="flex flex-col justify-center items-center">
            <LazyLoad>
              <img
                src={i1}
                className="flex justify-center items-center mt-4 g-img"
              />
            </LazyLoad>
            <LazyLoad once>
              <img src={i2} className="mt-4 g-img" />
            </LazyLoad>
            <LazyLoad once>
              <img src={i3} className="mt-4 g-img" />
            </LazyLoad>
            <LazyLoad once>
              <img src={i4} className="mt-4 g-img" />
            </LazyLoad>
            <LazyLoad once>
              <img src={i5} className="mt-4 g-img" />
            </LazyLoad>
            <LazyLoad once>
              <img src={i6} className="mt-4 g-img" />
            </LazyLoad>
            <LazyLoad once>
              <img src={i7} className="mt-4 g-img" />
            </LazyLoad>
            <LazyLoad once>
              <img src={i8} className="mt-4 g-img" />
            </LazyLoad>
          </div>
        </section>
      </main>
    );
  }
}
